import React from 'react'
import styled from 'styled-components'
import Fade from 'react-reveal/Fade'

const Home = ({data}) => {
    return (
        <>{
            data.map(e => {
                return (
                    <Wrap defaultValue={e.image}>
                        
                        <Heading>
                            <Fade bottom>
                                <h1>{ e.title}</h1>
                            <p style={{ fontSize: "15px" }}>{e.description}</p>
                            </Fade>
                            </Heading>
                        
                        <Btn>
                            <Fade bottom>
                            <ButtonGroup>
                                <LeftBtn>{e.left}</LeftBtn>
                                {e.right && <RightBtn>{e.right}</RightBtn>}
                            </ButtonGroup>
                                <Arrow>
                                    <img src="/image/down-arrow.svg" width="30px;"></img>
                                </Arrow>
                            </Fade>
                            
                        </Btn>
                    </Wrap>
                )
            })
        }
            
        </>
    )
}

export default Home

const Wrap = styled.div`
    width:100%;
    height:100vh;
    background-image:${props => `url("/image/${props.defaultValue}")`};
    background-position:center;
    background-size:cover;
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    background-repeat:no-repeat;
    scroll-snap-align:start ;
`
const Heading = styled.div`
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:center;
    text-alignment:center;
    padding-top:15vh;
    font-size:20px;
`
const ButtonGroup = styled.div`
    display:flex;
    align-items:center;
    justify-content:center;
    @media only screen and (max-width:720px){
        flex-direction:column;
    }

`
const LeftBtn = styled.div`
    background:black;
    opacity:0.7;
    color:white;
    font-size:16px;
    padding:10px;
    border-radius:100px;
    width:250px;
    text-align:center;
    margin:15px;
    cursor:pointer;
    
`
const RightBtn = styled(LeftBtn)`
    background:white;
    color:black;
    cursor:pointer;
    
`
const Arrow = styled.div`
    display:flex;
    justify-content:center;
    padding-bottom:5px;
    cursor:pointer;
    img{
        display: inline-block;
        position: relative;
        -moz-animation: bounce 0.5s infinite linear;
        -o-animation: bounce 0.5s infinite linear;
        -webkit-animation: bounce 0.5s infinite linear;
        animation: bounce 0.5s infinite linear;
        colr:000;
    }
    @-webkit-keyframes bounce {
    0% { top: 0; }
    50% { top: -0.2em; }
    70% { top: -0.3em; }
    100% { top: 0; }
    }
    @-moz-keyframes bounce {
        0% { top: 0; }
        50% { top: -0.2em; }
        70% { top: -0.3em; }
        100% { top: 0; }
    }
    @-o-keyframes bounce {
        0% { top: 0; }
        50% { top: -0.2em; }
        70% { top: -0.3em; }
        100% { top: 0; }
    }
    @-ms-keyframes bounce {
        0% { top: 0; }
        50% { top: -0.2em; }
        70% { top: -0.3em; }
        100% { top: 0; }
    }
    @keyframes bounce {
        0% { top: 0; }
        50% { top: -0.2em; }
        70% { top: -0.3em; }
        100% { top: 0; }
    }
`
const Btn = styled.div`
    
    
`
