import React , {useState} from 'react'
import styled from 'styled-components'
import CloseIcon from '@material-ui/icons/Close';
import Fade from 'react-reveal/Fade'


const NavBar = () => {
    const [close,setClose] = useState(true)
    return (
        <Container>
            <Logo>
                <img src="./image/logo.svg"></img>
            </Logo>
            <Models>
                <a href="#">Model S</a>
                <a href="#">Model 3</a>
                <a href="#">Model x</a>
                <a href="#">Model y</a>
                <a href="#">Model Solar Panel</a>
                <a href="#">Model Solar Roof</a>
            </Models>
            <Menu>
                <a href="#" >Shope</a>
                <a href="#">Account</a>
                <a href="#" onClick={(e) => {
                    setClose(false)
                }}>Menu</a>
            </Menu>
            {close ||
                <SideMenu>
                    <ul>
                        <Icons onClick={() => {
                            setClose(true)
                        }}>
                            <Close/>
                        </Icons>
                        <li style={{ marginTop: "10px"}}>Existing Inventory</li>
                        <li>Used Inventory</li>
                        <li>Trade-In</li>
                        <li>Test Drive</li>
                        <li>Cybertruck</li>
                        <li>Roadster</li>
                        <li>Semi</li>
                        <li>Charging</li>
                        <li>Powerwall</li>
                        <li>Commercial Energy</li>
                        <li>Utilities</li>
                        <li>Find Us</li>
                        <li>Support</li>
                        <li style={{marginBottom: "50px" }}>Tesla Clone By <i style={{fontWeight:"600"}}>Mohamed Shaheed A</i></li>
                    </ul>
                    </SideMenu>
                }
        </Container>
    )
}

export default NavBar


const Container = styled.div`
    width:100%;
    height:60px;
    position:fixed;
    display:flex;
    align-items:center;
    justify-content:space-between;
    z-index:1;

`
const Logo = styled.div`
    display:flex;
    align-items:center;
    justify-content:flex-end;
    margin-left:50px;
    cursor:pointer;
    @media only screen and (max-width:720px){
        margin-left:10px;
    }

`

const Models = styled.div`
   @media only screen and (max-width:1200px){
       display:none;
   }

`

const Menu = styled.div`
    margin-right:20px;
    @media only screen and (max-width:720px){
        a{
            padding: 0 8px;
        }
    }
`

const SideMenu = styled.div`
   position:fixed;
   top:0;
   right:0;
   background:white;
   width:300px;
   height:100vh;
   z-index:100;
   li{
       list-style-type:none;
       margin-left:50px;
   }
   ul{
       display:flex;
       height:100vh;
       flex-direction:column;
       align-items:start;
       justify-content:space-between;
   }

// `
const Close = styled(CloseIcon)`
   cursor:pointer;
   
`
const Icons = styled.div`
   display:flex;
   width:300px;
   justify-content:flex-end;
   align-items:center;
   margin-top:20px;
   padding-right:20px;
`